.app__experience {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.experience {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  height: 20vh;
  transform: scale(0.75);
}

.Images {
  height: 7rem;
}
.achievement {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.circle {
  width: 5rem;
  height: 5rem;
  background: rgb(226, 125, 201);
  border-radius: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.circle::before {
  border-radius: 100%;
  content: "";
  background-image: linear-gradient(#db8813 30%, #1c9419 30%);
  top: -12px;
  left: -12px;
  bottom: -12px;
  right: -12px;
  position: absolute;
  z-index: -2;
  box-shadow: var(--boxShadow);
}

.text {
  font-size: 1.3rem;
  font-weight: 700;
  text-align: center;
  color: var(--black-color);
  text-transform: capitalize;

  span {
    color: var(--secondary-color);
  }

  @media screen and (min-width: 2000px) {
    font-size: 1rem;
  }

  @media screen and (max-width: 450px) {
    font-size: 1rem;
  }
}

.achievement span:nth-of-type(2) {
  color: var(--orange);
}

@media screen and (max-width: 480px) {
  .experience {
    transform: scale(0.7);
  }
}
