.app__about {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.app__profiles {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 1rem;
}

.header {
  font-size: 0.9rem;
  // font-weight: 700;
  // color: var(--black-color);
  text-align: left;
  line-height: 1.5;

  span {
    font-weight: 700;
    // color: var(--secondary-color);
  }

  @media screen and (min-width: 2000px) {
    font-size: 2rem;
  }

  @media screen and (max-width: 450px) {
    font-size: 0.9rem;
  }
}

.desc {
  font-size: 1rem;
  text-align: left;
  color: var(--gray-color);
  line-height: 1.5;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem;
  }
}

.link_text {
  font-size: 1.1rem;
  font-weight: 500;
  text-align: left;
  color: var(--lgray-color);
  line-height: 1.5;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem;
  }
}

.arrow {
  font-size: 1.7rem;
  font-weight: 500;
  text-align: left;
  color: var(--secondary-color);
  line-height: 1.5;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem;
  }
}

.line {
  font-size: 1rem;
  font-weight: 700;
  text-align: left;
  color: var(--black-color);
  line-height: 1.5;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem;
  }
}

.name {
  color: var(--gray-color);
}

.quote {
  font-size: 1rem;
  font-weight: 800;
  text-align: left;
  // color: var(--secondary-color);
  font-family:monospace;
  line-height: 1.5;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem;
  }
}

.app__profile-item {
  width: 710px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin: 2rem;

  img {
    justify-content: center;
    width: 25%;
    height: 175px;
    border-radius: 15px;
    object-fit: cover;
  }

  @media screen and (max-width: 1000px) {
    width: 320px;
    margin: 3rem 1rem;

    img {
      width: 50%;
      height: 160px;
    }
  }
}
